import Vue from "vue";
import Vuex from "vuex";
import App from "./App.vue";

Vue.config.productionTip = false;
Vue.use(Vuex);

const store = new Vuex.Store({
  state: {
    isPlaying: false,
    duration: 0,
    curProgress: 0,
    curPosition: 0,
    aIndex: 0,
    bIndex: 0,
  },
  mutations: {
    updateStatus(state, st) {
      Object.assign(state, st);
    },
  },
});
new Vue({
  render: (h) => h(App),
  store: store,
}).$mount("#app");
